html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}
body,
html {
  height: 100%;
  margin: 0;
}
.app .weui-btn {
  font-size: .453333rem;
}
.app .vux-label {
  font-size: .426667rem;
}
.app .weui-cell {
  padding-top: 0;
  padding-bottom: 0;
}
.app .weui-cell_select .weui-select {
  padding-right: .533333rem;
}
.app .weui-cell_access .weui-cell__ft {
  padding-right: .466667rem;
  text-align: left;
}
.app .weui-cell_access p {
  width: 105px;
  text-align: left;
}
.app .weui-toast__content {
  color: #fff !important;
}
.app .weui-cell__ft i {
  font-size: .426667rem;
}
.app .weui-cell_select .weui-cell__bd:after {
  right: 0.42rem;
}
.page-register {
  background-color: #fff;
  height: 100%;
}
.page-register__content {
  padding: 0 .333333rem 1.333333rem .333333rem;
  position: absolute;
  top: 1.333333rem;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto;
}
.page-register__content h5 {
  text-align: center;
  margin: 0;
  padding-top: .666667rem;
  font-size: .506667rem;
  font-weight: 400;
}
.page-register__content p {
  text-align: center;
  margin: 0;
  padding-bottom: 0;
  font-size: .32rem;
  color: #666;
}
.page-register__content .weui-cell {
  height: 1.333333rem;
  font-size: .4rem;
}
.page-register__content .weui-cell p {
  color: #000;
  font-size: .4rem;
}
.page-register__content .weui-btn {
  height: 1.2rem;
  margin-top: .6rem;
}
#app .app__content {
  position: absolute;
  top: 0;
  bottom: 1.386667rem;
  left: 0;
  right: 0;
}
#app .app__bottom-tab .weui-tabbar {
  height: 1.386667rem;
}
#app .app__bottom-tab .weui-tabbar__item {
  padding: 0;
}
#app .app__bottom-tab .weui-tabbar__icon {
  width: .666667rem;
  height: .666667rem;
}
#app .app__bottom-tab .weui-tabbar__label {
  margin-top: .066667rem;
  font-size: .266667rem;
}
#app .app__bottom-tab .iconfont {
  font-size: .666667rem;
}



